.profile {
  display: flex;
 }  //gap: 30px

.profile__imgWrapper {
  width: 30%; }

.profile__info {
  width: 70%;
  display: flex;
  justify-content: space-between;
  flex-direction: column; }

.profile__input {
  width: 100%; }

.profile__form {
  display: flex; }

.profile__item {
  margin-bottom: 10px;
  display: flex;
  gap: 10px;
  align-items: center; }

.profile__status {
  font-weight: 600; }

.profile__statusWrapper {
  margin-top: 3px;
  margin-bottom: 20px;
  display: flex;
  gap: 5px; }

.profile__statusInput {
  margin-top: -4px;
  border: 1px solid darkgrey;
  border-radius: 2px;
  width: 400px;
  padding: 3px; }
