.post {
  display: flex;
  gap: 20px;
  margin-bottom: 40px;
  border: 1px solid rgb(222, 219, 219);
  padding: 20px;
  border-radius: 4px;
  height: 120px; }

.post__content {
  width: 70%;
  min-height: 100%;
  display: flex;
  text-align: left;
  flex-direction: column; }

.post__published {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 10px;
  width: 10%; }

.post__header {
  margin-bottom: 10px;
  font-size: 24px;
  font-weight: 500; }

.post__desc {
  margin-bottom: 20px;
  line-height: 20px;
  flex: 1 1 auto; }

.post__buttons {
  display: flex;
  justify-content: flex-end;
  gap: 20px;
  align-items: center; }
